'use client'
import React, { useState } from 'react';
import emailjs from '@emailjs/browser';

// Initialisez EmailJS avec votre public key
emailjs.init("VOTRE_PUBLIC_KEY") // Remplacez par votre vraie clé

const ContactPage = () => {
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        subject: '',
        message: ''
    });
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [submitStatus, setSubmitStatus] = useState<'idle' | 'success' | 'error'>('idle');

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };

    const handleSubmit = async (e: React.FormEvent) => {
      e.preventDefault()
      setIsSubmitting(true)
      
      try {
        
        const templateParams = {
          from_name: formData.name,      // ✅ Correspond à {{from_name}}
          from_email: formData.email,    // ✅ Correspond à {{from_email}}
          subject: formData.subject,     // ✅ Correspond à {{subject}}
          message: formData.message,     // ✅ Correspond à {{message}}
        }
        
        console.log('Envoi avec:', templateParams)
        
        const result = await emailjs.send(
          'service_az3u2eg',     // Votre Service ID
          'template_ftj3abi',    // Votre Template ID
          templateParams
        )
        
        console.log('SUCCESS!', result)
        setSubmitStatus('success')
        setFormData({ name: '', email: '', subject: '', message: '' })
        
      } catch (error) {
        console.error('Erreur:', error)
        setSubmitStatus('error')
      } finally {
        setIsSubmitting(false)
        setTimeout(() => setSubmitStatus('idle'), 5000)
      }
    };

    return (
        <div className="pt-16 min-h-screen">
            {/* Hero Section */}
            <section className="py-20 relative">
                <div className="absolute inset-0 bg-gradient-to-r from-cyber-cyan/5 to-cyber-purple/5 opacity-30"></div>
                
                <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
                    <div className="text-center mb-16">
                        <h1 className="font-cyber-title text-4xl md:text-6xl font-bold text-white mb-6">
                            Contactez-<span className="text-cyber-cyan">moi</span>
                        </h1>
                        <p className="text-gray-300 text-xl max-w-2xl mx-auto">
                            Une idée de projet ? Une opportunité ? N'hésitez pas à me contacter. 
                            Je suis toujours ouvert aux discussions autour de nouvelles technologies et projets innovants.
                        </p>
                    </div>
                </div>
            </section>

            <section className="py-16">
                <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8">
                    <div className="grid lg:grid-cols-2 gap-16">
                        
                        {/* Informations de contact */}
                        <div className="space-y-8">
                            <div>
                                <h2 className="font-cyber-title text-2xl font-bold text-cyber-cyan mb-6">
                                    Mes informations
                                </h2>
                                
                                <div className="space-y-6">
                                    <div className="cyber-card p-6 cyber-glow-hover">
                                        <div className="flex items-center space-x-4">
                                            <div className="w-12 h-12 bg-cyber-cyan/10 rounded-lg flex items-center justify-center">
                                                <svg className="w-6 h-6 text-cyber-cyan" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                                                </svg>
                                            </div>
                                            <div>
                                                <h3 className="text-white font-semibold">Contact</h3>
                                                <p className="text-gray-300">Formulaire sécurisé ci-contre</p>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="cyber-card p-6 cyber-glow-hover">
                                        <div className="flex items-center space-x-4">
                                            <div className="w-12 h-12 bg-cyber-cyan/10 rounded-lg flex items-center justify-center">
                                                <svg className="w-6 h-6 text-cyber-cyan" fill="currentColor" viewBox="0 0 24 24">
                                                    <path d="M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.30.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z"/>
                                                </svg>
                                            </div>
                                            <div>
                                                <h3 className="text-white font-semibold">GitHub</h3>
                                                <a href="https://github.com/Axo34080" target="_blank" rel="noopener noreferrer" className="text-cyber-cyan hover:text-white transition-colors">
                                                    github.com/Axo34080
                                                </a>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="cyber-card p-6 cyber-glow-hover">
                                        <div className="flex items-center space-x-4">
                                            <div className="w-12 h-12 bg-cyber-cyan/10 rounded-lg flex items-center justify-center">
                                                <svg className="w-6 h-6 text-cyber-cyan" fill="currentColor" viewBox="0 0 24 24">
                                                    <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
                                                </svg>
                                            </div>
                                            <div>
                                                <h3 className="text-white font-semibold">LinkedIn</h3>
                                                <a href="#" className="text-cyber-cyan hover:text-white transition-colors">
                                                    linkedin.com/in/marc-villain
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* Disponibilité */}
                            <div className="cyber-card p-6 border-cyber-green/20 bg-cyber-green/5">
                                <h3 className="font-cyber-title text-lg font-bold text-cyber-green mb-3">
                                    Actuellement disponible
                                </h3>
                                <p className="text-gray-300 text-sm">
                                    En recherche d'opportunités pour un Master en alternance.
                                    Spécialisation développement d'applications et site web.
                                </p>
                            </div>
                        </div>

                        {/* Formulaire de contact */}
                        <div>
                          <h2 className="font-cyber-title text-2xl font-bold text-cyber-cyan mb-6">
                            Envoyez-moi un message
                          </h2>
                          
                          <form onSubmit={handleSubmit} className="space-y-6">
                            <div className="grid md:grid-cols-2 gap-6">
                              <div>
                                <label htmlFor="name" className="block text-sm font-medium text-gray-300 mb-2">
                                  Nom complet
                                </label>
                                <input
                                  type="text"
                                  id="name"
                                  name="name"
                                  value={formData.name}
                                  onChange={handleChange}
                                  required
                                  className="w-full px-4 py-3 bg-cyber-dark/50 border border-cyber-cyan/30 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-cyber-cyan focus:border-transparent transition-all"
                                  placeholder="Votre nom"
                                />
                              </div>
                              
                              <div>
                                <label htmlFor="email" className="block text-sm font-medium text-gray-300 mb-2">
                                  Email
                                </label>
                                <input
                                  type="email"
                                  id="email"
                                  name="email"
                                  value={formData.email}
                                  onChange={handleChange}
                                  required
                                  className="w-full px-4 py-3 bg-cyber-dark/50 border border-cyber-cyan/30 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-cyber-cyan focus:border-transparent transition-all"
                                  placeholder="votre@email.com"
                                />
                              </div>
                            </div>

                            <div>
                              <label htmlFor="subject" className="block text-sm font-medium text-gray-300 mb-2">
                                Sujet
                              </label>
                              <input
                                type="text"
                                id="subject"
                                name="subject"
                                value={formData.subject}
                                onChange={handleChange}
                                required
                                className="w-full px-4 py-3 bg-cyber-dark/50 border border-cyber-cyan/30 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-cyber-cyan focus:border-transparent transition-all"
                                placeholder="Sujet de votre message"
                              />
                            </div>

                            <div>
                              <label htmlFor="message" className="block text-sm font-medium text-gray-300 mb-2">
                                Message
                              </label>
                              <textarea
                                id="message"
                                name="message"
                                value={formData.message}
                                onChange={handleChange}
                                required
                                rows={6}
                                className="w-full px-4 py-3 bg-cyber-dark/50 border border-cyber-cyan/30 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-cyber-cyan focus:border-transparent transition-all resize-none"
                                placeholder="Décrivez votre projet ou votre demande..."
                              />
                            </div>

                            <button
                              type="submit"
                              disabled={isSubmitting}
                              className={`w-full px-8 py-4 rounded-lg font-semibold transition-all duration-300 ${
                                isSubmitting 
                                  ? 'bg-gray-600 text-gray-300 cursor-not-allowed'
                                  : 'bg-cyber-cyan text-cyber-dark hover:bg-white shadow-cyber-glow hover:shadow-cyber-glow-strong'
                              }`}
                            >
                              {isSubmitting ? (
                                <div className="flex items-center justify-center space-x-2">
                                  <svg className="animate-spin w-5 h-5" fill="none" viewBox="0 0 24 24">
                                    <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" className="opacity-25"></circle>
                                    <path fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" className="opacity-75"></path>
                                  </svg>
                                  <span>Envoi en cours...</span>
                                </div>
                              ) : (
                                'Envoyer le message'
                              )}
                            </button>

                            {/* Messages de statut */}
                            {submitStatus === 'success' && (
                              <div className="p-4 bg-cyber-green/10 border border-cyber-green/30 rounded-lg text-cyber-green">
                                ✅ Message envoyé avec succès ! Je vous répondrai dans les plus brefs délais.
                              </div>
                            )}

                            {submitStatus === 'error' && (
                              <div className="p-4 bg-red-500/10 border border-red-500/30 rounded-lg text-red-400">
                                ❌ Erreur lors de l'envoi. Veuillez réessayer ou me contacter directement par email.
                              </div>
                            )}
                          </form>
                        </div>
                    </div>
                </div>
            </section>


        </div>
    );
};

export default ContactPage;